@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Lato', sans-serif;
	background-color: #333;
	color: #222;
	display: flex;
	overflow-x: hidden;
}

.container {
	background-color: #fafafa;
	width: 100vw;
	min-height: 100vh;
	padding: 50px;
	transform-origin: top left;
	transition: transform 0.7s linear;
}

.container.show-nav {
	transform: rotate(-20deg);
}

.circle-container {
	position: fixed;
	top: -100px;
	left: -100px;
}

.circle {
	background-color: #ff7979;
	width: 200px;
	height: 200px;
	border-radius: 50%;
	position: relative;
	transition: transform 0.5s linear;
}

.container.show-nav .circle {
	transform: rotate(-70deg);
}

.circle button {
	position: absolute;
	top: 50%;
	left: 50%;
	height: 100px;
	background: transparent;
	font-size: 26px;
	border: 0;
	color: #fff;
	cursor: pointer;
}

.circle button:focus {
	outline: none;
}

.circle button#open {
	left: 60%;
}

.circle button#close {
	top: 65%;
	transform: rotate(90deg);
	transform-origin: top left;
}
.content {
	max-width: 1000px;
	margin: 50px auto;
}

.content h1 {
	margin: 0;
}

.content h3 {
	margin: 10px auto;
}

.content small {
	display: inline-block;
	color: #555;
	font-style: italic;
	margin-bottom: 10px;
}

.content p {
	color: #333;
	line-height: 1.5;
}

.content img {
	max-width: 100%;
}

.content.active {
	transform-origin: top;
	transform: rotate(-10deg);
}

nav {
	position: fixed;
	bottom: 40px;
	left: 0;
	z-index: 100;
}

.container.show-nav + nav li {
	transform: translateX(0);
	transition-delay: 0.3s;
}

nav ul {
	list-style-type: none;
	padding: 30px;
}

nav ul li {
	text-transform: uppercase;
	color: #fff;
	margin: 40px 0;
	transform: translateX(-100%);
	transition: transform 0.4s ease-in;
}

nav ul li i {
	font-size: 20px;
	margin-right: 10px;
}

nav ul li + li {
	margin-left: 15px;
	transform: translateX(-150%);
}

nav ul li + li + li {
	margin-left: 30px;
	transform: translateX(-200%);
}
